}

.ButtonContainer {
    padding-top: 5px;
    margin-top: 3vw;
}

.SaveButton {
    width: 20vw;
    min-height: 3vw;
    margin: 2.5vw;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #4CAF50;
    color: white;
    font-size: 2vw;
    border-radius: 2em;
    text-shadow: 2px 2px 4px #000000;
}

.fancyText {
    color: #5bc0de;
    font-size: 2em;
    text-align: center;
}

.ScoreDisplay {
    display: inline-block;
    text-align: center;
    align-items: center;
    line-height: calc(5vw - 0.5vw - 0.5vh);
    line-height: calc(var(--pieceSize) - 0.5vw - 0.5vh);
    margin-right: 3vw;
    margin-left: 1.5vw;
    font-size: 2vw;
    font-weight: bold;
    color: white;
    text-shadow: 2px 2px 4px #000000;
}

.ghost {
